import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Header from "../header/Header"
import Subheader from "../subHeader/subHeader"
import Bargraph from "../bargraph/Bargraph"
import "./Children.scss"


class Children extends Component {
	render() {
        var description;
        var page = this.props.location.state.page;
        if (page === "all"){
            description = "Frequency of number of children for all of your ancestors";
        } else if (page === "mother"){
            description = "Frequency of number of children on your mother's side";
        } else {
            description = "Frequency of number of children on your father's side";
        }
		return (
			<div>
                <Header name=" - Children" home="true"/>
                <div className="children-header">Children per Mother</div>

                <Bargraph title=""
                 description="Frequency of number of children for all of your ancestors"
                 y="Mothers"
                 x = "Number of Children"
                 rowCount="8"
                 linkTo="personRows"
                 page={page}
                 hiddenClass=""/>

                <Bargraph title=""
                description="Frequency of number of children on your mother's side"
                y="Mothers"
                x = "Number of Children"
                rowCount="8"
                linkTo="personRows"
                page={page}
                hiddenClass="hidden"/>
            
                <Bargraph title=""
                description="Frequency of number of children on your father's side"
                y="Mothers"
                x = "Number of Children"
                rowCount="8"
                linkTo="personRows"
                page={page}
                hiddenClass="hidden"/>
			</div>
		);
	}
}

export default Children;