import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Bargraph.scss';

class Bargraph extends Component {


    componentDidUpdate(){
        const charts = document.getElementsByClassName("bargraph");
        for (var c = 0; c < charts.length; c++){
            var chart = charts[c];
            const bars = chart.querySelectorAll(".bar");
            if (this.props.title == "Birthdays by Month"){
                var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul",'Aug',"Sep","Oct","Nov","Dec"];
                for (var i = 0; i < bars.length; i++) {
                    bars[i].dataset.title =  months[i];
                }
            } else if (this.props.title == "Lifespans") {
                var spans = ["20-30","31-40","41-50","51-60","61-70","71-80","81-90","91-100","100+"];
                for (var i = 0; i < bars.length; i++) {
                    bars[i].dataset.title =  spans[i];
                }
            } else if (this.props.title == "Most Common Shared Birthdays"){
                
            } else  {
                for (var i = 0; i < bars.length; i++) {
                    bars[i].dataset.title =  i+1;
                }
            }
        
            function populateChart() {
                for (let bar of bars) {
                    let val = bar.dataset.val;
                    setValOfBar(val, bar);
                }
            }
            
            function setValOfBar(val, bar) {
                if (val > 11){
                    bar.style.height = val * 10 + "px";
                } else {
                    bar.style.height = val * 20 + "px";
                }
                
                bar.classList.toggle("flip-percentage", val < 50);
            }
            
            populateChart();
        }

        var animateHTML = function() {
            var elems;
            var windowHeight;
            function init() {
              elems = document.querySelectorAll('.hidden');
              windowHeight = window.innerHeight;
              addEventHandlers();
              checkPosition();
            }
            function addEventHandlers() {
              window.addEventListener('scroll', checkPosition);
              window.addEventListener('resize', init);
            }
            function checkPosition() {
              for (var i = 0; i < elems.length; i++) {
                var positionFromTop = elems[i].getBoundingClientRect().top;
                if (positionFromTop - windowHeight <= 0) {
                  elems[i].className = elems[i].className.replace(
                    'hidden',
                    'fade-in-element'
                  );
                }
              }
            }
            return {
              init: init
            };
          };
          animateHTML().init();
    }

    componentDidMount(){
        const charts = document.getElementsByClassName("bargraph");
        for (var c = 0; c < charts.length; c++){
            var chart = charts[c];
            const bars = chart.querySelectorAll(".bar");
            if (this.props.title == "Birthdays by Month"){
                var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul",'Aug',"Sep","Oct","Nov","Dec"];
                for (var i = 0; i < bars.length; i++) {
                    bars[i].dataset.title =  months[i];
                }
            } else if (this.props.title == "Lifespans") {
                var spans = ["20-30","31-40","41-50","51-60","61-70","71-80","81-90","91-100","100+"];
                for (var i = 0; i < bars.length; i++) {
                    bars[i].dataset.title =  spans[i];
                }
            } else if (this.props.title == "Most Common Shared Birthdays"){
                
            } else  {
                for (var i = 0; i < bars.length; i++) {
                    bars[i].dataset.title =  i+1;
                }
            }
        
            function populateChart() {
                for (let bar of bars) {
                    let val = bar.dataset.val;
                    setValOfBar(val, bar);
                }
            }
            
            function setValOfBar(val, bar) {
                if (val > 11){
                    bar.style.height = val * 10 + "px";
                } else {
                    bar.style.height = val * 20 + "px";
                }
                
                bar.classList.toggle("flip-percentage", val < 50);
            }
            
            populateChart();
        }

        var animateHTML = function() {
            var elems;
            var windowHeight;
            function init() {
              elems = document.querySelectorAll('.hidden');
              windowHeight = window.innerHeight;
              addEventHandlers();
              checkPosition();
            }
            function addEventHandlers() {
              window.addEventListener('scroll', checkPosition);
              window.addEventListener('resize', init);
            }
            function checkPosition() {
              for (var i = 0; i < elems.length; i++) {
                var positionFromTop = elems[i].getBoundingClientRect().top;
                if (positionFromTop - windowHeight <= 0) {
                  elems[i].className = elems[i].className.replace(
                    'hidden',
                    'fade-in-element'
                  );
                }
              }
            }
            return {
              init: init
            };
          };
          animateHTML().init();
      
    }
  render() {
    var rows = [];
    var props = {};
    var dataTitle = [];
    if (this.props.title == "Lifespans"){
        props.page = this.props.page;
        props.headerType = "normal";
        props.title1 = "People who died between ";
        for (var i = 0; i < this.props.rowCount; i++) {
            let val = Math.floor(Math.random() * 12);
    
            rows.push(<Link data-val={val} to={{pathname: "/"+this.props.linkTo, state:{props: props, i: i, value: val, date:dataTitle[i]}}} data-title={dataTitle[i]}  className="bar"></Link>);
        }
    } else if (this.props.title == "Most Common Shared Birthdays"){
        props.page = " - Birthdays";
        props.headerType = "birthdays";
        props.title1 = "People who have a birthday on ";
        props.tab = "main";
        function randomDate(start, end) {
            var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul",'Aug',"Sep","Oct","Nov","Dec"];
            var month = Math.floor(Math.random() * 12);
            var day = Math.floor(Math.random() * 30) + 1;
        
            return months[month] + "  " + day;
        }
        var oldNumber = Math.floor(Math.random() * 5) + 25;
        for (var i = 0; i < this.props.rowCount; i++) {
            var date = randomDate(new Date(2019, 0, 1), new Date(2020, 0, 1));
            dataTitle[i]=  date;
            let val = oldNumber - Math.floor(Math.random() * 3);
            oldNumber = val;
            rows.push(<Link data-val={val} to={{pathname: "/"+this.props.linkTo, state:{props: props, i: i, value: val, date:dataTitle[i]}}} data-title={dataTitle[i]}  className="bar"></Link>);
        }
    } else {
        props.page = this.props.page;
        props.headerType = "normal";
        props.title1 = "Women who had ";
        props.title2 = " children"
        for (var i = 0; i < this.props.rowCount; i++) {
            let val = Math.floor(Math.random() * 12);
    
            rows.push(<Link data-val={val} to={{pathname: "/"+this.props.linkTo, state:{props: props, i: i, value: val, date:dataTitle[i]}}} data-title={dataTitle[i]}  className="bar"></Link>);
        }
    }
    var classname = "bargraphWrapper " + this.props.hiddenClass;
    return (
        <div className={classname}>
            <div className="bargraphTitle">{this.props.title}</div>
            <div className="graphDescription">{this.props.description}</div>
            <div className="yAxis">{this.props.y}</div>
            <div className="bargraph">
                {rows}
            </div>
            <div className="xAxis">{this.props.x}</div>
            <div className="barInstructions">Click a bar</div>
        </div>
    );
  }
  
}

export default Bargraph;
